/* DSNOWPresentation.css */
.min-h-screen {
  min-height: 100vh;
}

.bg-gray-50 {
  background-color: #f9fafb;
}

.p-6 {
  padding: 1.5rem;
}

.max-w-5xl {
  max-width: 64rem;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.bg-white {
  background-color: #ffffff;
}

.rounded-lg {
  border-radius: 0.5rem;
}

.shadow-md {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.overflow-hidden {
  overflow: hidden;
}

.bg-blue-700 {
  background-color: #1d4ed8;
}

.text-white {
  color: #ffffff;
}

.text-3xl {
  font-size: 1.875rem;
  line-height: 2.25rem;
}

.font-bold {
  font-weight: 700;
}

.text-xl {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.mt-2 {
  margin-top: 0.5rem;
}

.text-blue-100 {
  color: #dbeafe;
}

.bg-gray-100 {
  background-color: #f3f4f6;
}

.p-4 {
  padding: 1rem;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.gap-2 {
  gap: 0.5rem;
}

.px-4 {
  padding-left: 1rem;
  padding-right: 1rem;
}

.py-2 {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.rounded-md {
  border-radius: 0.375rem;
}

.text-sm {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.font-medium {
  font-weight: 500;
}

.transition-colors {
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.bg-blue-600 {
  background-color: #2563eb;
}

.bg-white {
  background-color: #ffffff;
}

.text-gray-700 {
  color: #374151;
}

.hover\:bg-gray-200:hover {
  background-color: #e5e7eb;
}

.text-2xl {
  font-size: 1.5rem;
  line-height: 2rem;
}

.mb-4 {
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.text-gray-600 {
  color: #4b5563;
}

/* Lists */
.list-disc {
  list-style-type: disc;
}

.pl-6 {
  padding-left: 1.5rem;
}

.space-y-2 > * + * {
  margin-top: 0.5rem;
}

/* Charts */
.h-72 {
  height: 18rem;
}

/* Code block */
.bg-gray-900 {
  background-color: #111827;
}

.rounded-md {
  border-radius: 0.375rem;
}

.overflow-auto {
  overflow: auto;
}

.my-4 {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

/* Sections */
.my-8 {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.p-6 {
  padding: 1.5rem;
}

.bg-blue-50 {
  background-color: #eff6ff;
}

.border {
  border-width: 1px;
}

.border-blue-100 {
  border-color: #dbeafe;
}

.text-lg {
  font-size: 1.125rem;
  line-height: 1.75rem;
}

.text-blue-800 {
  color: #1e40af;
}

/* Grid */
.grid {
  display: grid;
}

.grid-cols-1 {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.gap-4 {
  gap: 1rem;
}

@media (min-width: 768px) {
  .md\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
  
  .md\:flex-row {
    flex-direction: row;
  }
  
  .md\:space-y-0 {
    margin-top: 0;
  }
  
  .md\:space-x-4 > * + * {
    margin-left: 1rem;
  }
}

.shadow-sm {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

/* Colors for feature importance */
.bg-blue-500 {
  background-color: #3b82f6;
}

.bg-green-500 {
  background-color: #22c55e;
}

.bg-yellow-500 {
  background-color: #eab308;
}

.bg-red-500 {
  background-color: #ef4444;
}

.bg-purple-500 {
  background-color: #a855f7;
}

.w-4 {
  width: 1rem;
}

.h-4 {
  height: 1rem;
}

.space-x-2 > * + * {
  margin-left: 0.5rem;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

/* Links */
.text-blue-600 {
  color: #2563eb;
}

.hover\:underline:hover {
  text-decoration: underline;
}